.project {
  flex-basis: 100vh;
  background-color: black;
  display: flex;
  z-index: 1;
  &__github {
    height: 100% !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;

    &-text {
      font-size: 2rem;
      color: white;
    }
  }
  &__slide {
    background-repeat: no-repeat;
    background-position: center;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    &-text {
      font-size: 2rem;
      color: white;
    }
  }
  &__charcounter {
    background-image: url("../img/charcounter-mobile.PNG");
    @media (min-width: 768px) {
      background-image: url("../img/charcounter-desktop.PNG");
    }
  }
  &__todolist {
    background-image: url("../img/todolist-mobile.PNG");
    @media (min-width: 768px) {
      background-image: url("../img/todolist-desktop.PNG");
    }
  }
  & .fab {
    font-size: 10rem;
    color: white;
  }
}

.container-fluid {
  padding: 0 !important;
  flex-basis: 100% !important;
}

.carousel {
  height: 100% !important;
}
.carousel-item {
  height: 100% !important;
}
.view {
  height: 100% !important;
}
